import React, { useEffect } from "react";

import {
  Panel,
  PanelHeader,
  Button,
  Div,
  Group,
  FormItem,
  NativeSelect,
} from "@vkontakte/vkui";

//constants
import { APP_IMG_SHARING_STORIES, APP_IMG_SHARING_WALL } from "../constants";
import { nativeAds } from "../ads";
import { sharing } from "../sharing-method";

const ResultPanel = ({ id, go, IMGresult, openBase, getPlatform }) => {
  useEffect(() => {
    setTimeout(() => {
      // nativeAds();
    }, 1000);
  }, []);

  return (
    <Panel id={id}>
      <PanelHeader
        separator={false}
        left={
          <Div
            style={{
              display: "flex",
              alignItems: "center",
              justifyContent: "center",
              flexDirection: "row",
            }}
          >
            <Div
              // onClick={AddToCommunity}
              style={{
                background:
                  "linear-gradient(90deg, rgba(247,106,171,1) 0%, rgba(0,212,255,1) 100%)",
                borderRadius: "9px",
                marginLeft: "10px",
                fontSize: "14px",
                height: "28px",
                width: "170px",
                color: "white",
                display: "flex",
                alignItems: "center",
                justifyContent: "center",
                userSelect: "none",
              }}
            >
              Поделиться в истории
            </Div>

            <Div></Div>
          </Div>
        }
      ></PanelHeader>

      <Div
        className={
          getPlatform === "web" ? "ResultPanelWeb" : "ResultPanelMobile"
        }
        style={{
          paddingBottom: "20px",
        }}

        // style={{
        //   display: "flex",
        //   flexDirection: "column",
        //   alignItems: "center",
        //   justifyContent: "space-around",
        //   // height: "450px",

        //   // marginTop: "150px",
        // }}
      >
        <Div
          style={{
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            justifyContent: "center",

            // marginTop: "150px",
          }}
        >
          {IMGresult}

          <Div
            style={{
              display: "flex",
              flexDirection: "row",
              alignItems: "center",
              justifyContent: "space-between",
              width: "300px",

              // marginTop: "150px",
            }}
          >
            <Div
              style={{
                color: "white",
                fontSize: "15px",
                fontWeight: "bold",
              }}
            >
              ПРИЧИНА СМЕРТИ
            </Div>
            <Div>
              {" "}
              <Button
                // onClick={openBase}
                onClick={() =>
                  sharing(
                    "story",
                    null,
                    null,
                    APP_IMG_SHARING_STORIES[globalURLSharing.url]
                  )
                }
                style={{
                  width: "60px",
                  height: "20px",
                  backgroundColor: "white",
                  marginTop: "5px",
                  borderRadius: "10px",
                }}
              >
                <span
                  style={{
                    color: "black",
                    fontSize: "13px",
                    fontWeight: "bold",
                  }}
                >
                  Узнать
                </span>
              </Button>
            </Div>
          </Div>

          <Div
            style={{
              display: "flex",
              flexDirection: "row",
              alignItems: "center",
              justifyContent: "space-between",
              width: "300px",

              // marginTop: "150px",
            }}
          >
            <Div
              style={{
                color: "white",
                fontSize: "15px",
                fontWeight: "bold",
              }}
            >
              ИМЯ СУЖЕНОГО
            </Div>
            <Div>
              {" "}
              <Button
                // onClick={openBase}
                onClick={() =>
                  sharing(
                    "story",
                    null,
                    null,
                    APP_IMG_SHARING_STORIES[globalURLSharing.url]
                  )
                }
                style={{
                  width: "60px",
                  height: "20px",
                  backgroundColor: "white",
                  marginTop: "5px",
                  borderRadius: "10px",
                }}
              >
                <span
                  style={{
                    color: "black",
                    fontSize: "13px",
                    fontWeight: "bold",
                  }}
                >
                  Узнать
                </span>
              </Button>
            </Div>
          </Div>

          <Div
            style={{
              display: "flex",
              flexDirection: "row",
              alignItems: "center",
              justifyContent: "space-between",
              width: "300px",

              // marginTop: "150px",
            }}
          >
            <Div
              style={{
                color: "white",
                fontSize: "15px",
                fontWeight: "bold",
              }}
            >
              МЕСТО ПЕРВОГО/СЛЕДУЮЩЕГО СЕКСА
            </Div>
            <Div>
              {" "}
              <Button
                // onClick={openBase}
                onClick={() =>
                  sharing(
                    "story",
                    null,
                    null,
                    APP_IMG_SHARING_STORIES[globalURLSharing.url]
                  )
                }
                style={{
                  width: "60px",
                  height: "20px",
                  backgroundColor: "white",
                  marginTop: "5px",
                  borderRadius: "10px",
                }}
              >
                <span
                  style={{
                    color: "black",
                    fontSize: "13px",
                    fontWeight: "bold",
                  }}
                >
                  Узнать
                </span>
              </Button>
            </Div>
          </Div>

          {/* <Div>
            <Button
              // onClick={openBase}
              onClick={() =>
                sharing(
                  "story",
                  null,
                  null,
                  APP_IMG_SHARING_STORIES[globalURLSharing.url]
                )
              }
              style={{
                width: "300px",
                height: "40px",
                backgroundColor: "white",
                marginTop: "5px",
                borderRadius: "10px",
              }}
            >
              <span
                style={{
                  color: "black",
                  fontSize: "15px",
                  fontWeight: "bold",
                }}
              >
                Поделиться в истории
              </span>
            </Button>
          </Div> */}
          <Div>
            <Button
              onClick={go}
              data-to="home"
              style={{
                width: "300px",
                height: "40px",
                backgroundColor: "white",
                marginTop: "5px",
                borderRadius: "10px",
              }}
            >
              <span
                style={{
                  color: "black",
                  fontSize: "15px",
                  fontWeight: "bold",
                }}
              >
                Пройти еще раз
              </span>
            </Button>
          </Div>
        </Div>
      </Div>
    </Panel>
  );
};

export { ResultPanel };
